@import url('https://fonts.googleapis.com/css2?family=Arimo:ital,wght@0,400..700;1,400..700&family=Geologica:wght@100..900&display=swap');

body {
    margin: 0;
    padding: 0;
    background: black;
    color: rgb(142, 142, 147);
    user-select: none;

    font-family: "Geologica", sans-serif;
    font-optical-sizing: auto;
    font-style: normal;

}

*, *::before, *::after {
    box-sizing: border-box;
}

img {
    box-sizing: border-box;
    height: auto;
    transition: opacity .75s;
}

#root {
    display: flex;
    justify-content: center;
}

.grid {
    display: grid;
    width: 100%;
    overflow: hidden;
    grid-template-columns: repeat(auto-fit, minmax(20%, 1fr));

    > img {
        opacity: .2;
        width: 100%;
        height: auto;
        object-fit: cover;

        &.bright {
            opacity: 1;
        }
    }

    &.loaded {
        > img {
            opacity: 1;
        }
    }
}

.notice {
    display: block;
    font-size: .95rem;
    text-align: center;
    background: rgb(28, 28, 30);
    padding: 1rem;
    margin: 2rem 0 0;
    border-radius: 1rem
}

.notice {
    h3 {
        margin: .75rem 0;
        padding: 0;
        color: rgb(229, 229, 234);
    }

    p {
        font-size: .9rem;
        margin: .5rem;
    }
}

.recommendations {
    margin-bottom: 5rem;

    .track {
        display: flex;
        position: relative;
        max-width: 400px;
        align-items: center;
        flex-direction: column;
        text-align: center;
        margin: 2rem;

        .track-actions {
            display: none;
            position: absolute;
            align-content: center;
            justify-content: center;
            flex-direction: column;
            width: 100%;
            height: 100%;
            background: rgba(0, 0, 0, .8);
            backdrop-filter: blur(4px);
            transition: background .5s ease, backdrop-filter 1.5s ease;
        }

        &.selected {
            .track-actions {
                display: flex;
            }
        }

        h3 {
            margin: .5rem;
            padding: 0;
            color: rgb(229, 229, 234);
        }

        p {
            margin: 0 0 1rem;
            padding: 0;
        }

        img {
            display: block;
            width: 90%;
            max-width: 320px;
            margin: 1rem;
        }
    }
}

.pause {
    opacity: .25;
    font-size: 10rem;
}

.controls {
    position: fixed;
    display: flex;
    align-items: center;
    box-sizing: border-box;
    bottom: 0;
    background: rgba(15, 15, 16, 0.75);
    backdrop-filter: blur(20px);
    width: 100%;
    padding: 1rem;

    button {
        font-size: .75rem;
    }

    .inner {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        max-width: 800px;
        margin: 0 auto;
    }

    @media all and (display-mode: standalone) {
        padding: 1rem 1rem 1.75rem;
    }
}

button {
    outline: none;
    border: none;
    border-radius: .25rem;
    padding: .75rem;
    background: transparent;
    color: rgb(242, 242, 247);
    font-weight: 700;
}

[data-tooltip] {
    position: relative;

    &:hover::before, &:focus::before, &:active::before {
        content: attr(data-tooltip);
        position: absolute;
        font-weight: 600;
        left: 50%;
        top: .4rem;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        transform: translateX(-50%);
        padding: .5rem;
        background: rgba(23, 23, 23, 0.47);
        backdrop-filter: blur(30px);
        color: rgb(142, 142, 147);
        border-radius: .25rem;
        font-size: .5rem;
    }
}

.btn-full {
    display: block;
    width: 100%;
}

.btn {
    display: inline-block;
    background: #FFD60A;
    color: black;
    padding: .75rem 1rem;
    border-radius: 2rem;
    font-weight: 700;
    text-decoration: none;
    font-size: .9rem;
    text-transform: uppercase;
    align-items: center;
    justify-content: center;
    gap: .5rem;
    cursor: pointer;
    transition: background .25s;

    &:hover {
        background: #FFD600;
    }

    &:active {
        background: #FFD500;
    }
}

.btn-spotify {
    display: inline-block;
    background: #1db954;
    color: white;
    padding: .75rem 1rem;
    border-radius: 2rem;
    font-weight: 700;
    text-decoration: none;
    font-size: .9rem;
    text-transform: uppercase;
    align-items: center;
    justify-content: center;
    gap: .5rem;
    cursor: pointer;
    transition: background .25s;

    &:hover {
        background: #1ed760;
    }

    &:active {
        background: #1aa34a;
    }
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, .98);
    backdrop-filter: blur(20px);
    z-index: 1000;
    padding: 3.5rem 2rem 1rem;
    overflow-y: scroll;

    > .close {
        position: absolute;
        top: 1rem;
        right: 1rem;
        font-size: .85rem;
        cursor: pointer;
    }
}

h1, h2, h3, h4, h5, h6 {
    margin: 0;
    padding: 0;
    color: rgb(229, 229, 234);
}

p {
    margin: 1rem 0;
}

input, .select > .placeholder {
    text-align: left;
    display: block;
    width: 100%;
    color: inherit;
    padding: 1rem;
    font-size: .9rem;
    outline: none;
    border: 1px solid rgba(142, 142, 147, .5);
    border-radius: 1rem;
    background: black;
}

.select {
    position: relative;

    > .dropdown {
        position: absolute;
        width: 100%;
        max-height: 200px;
        overflow-y: auto;
        background: rgba(28, 28, 30, .8);
        border-radius: 1rem;
        border: 1px solid rgba(229, 229, 234, .2);
        z-index: 1000;

        > div {
            padding: .5rem;
            cursor: pointer;
            transition: background .25s;

            &:hover {
                /*background: rgba(229, 229, 234, .2);*/
            }
        }
    }
}

.feature-list {
    p {
        margin: .75rem 0 0;
    }

    > div {
        position: relative;
        border-top: 1px solid rgba(229, 229, 234, .2);
        padding: 2rem 0;
        margin: 1rem 0;

        &:last-of-type {
            border-bottom: 1px solid rgba(229, 229, 234, .2);
        }
    }
}

.feature-window {
    transform: scale(.7);
    border: 1px solid rgba(229, 229, 234, .2);
    background: rgba(28, 28, 30, .8);
    padding: 1.2rem;
    border-radius: 1rem;
}

.promo {
    text-align: center;
    padding: 1rem;
    border-radius: 2rem;
    border: 1px solid rgba(229, 229, 234, .2);
    background-color: rgba(28, 28, 30, .8);

    .emp {
        color: white;
        font-weight: 600;
    }
}

